import img from '../../../images/contacto.jpeg'
import emailjs from 'emailjs-com'
import { useCallback, useState, useEffect } from 'react';
import { GoogleReCaptcha, useGoogleReCaptcha, GoogleReCaptchaConsumer } from 'react-google-recaptcha-v3';
import ReCAPTCHA from 'react-google-recaptcha';
import axios from 'axios';
const Contact = () => {

    const [showNotify,setShowNotify] = useState(false)
    const [checkCaptcha,setCheckCaptcha]= useState(false)
    
    
    const renderNotification =()=>{
        setTimeout(()=>setShowNotify(false),2000);
        return(
            <small className="text-success mx-auto fas fa-check"> Mensaje enviado </small>
        )
    }

    function sendMail(e){
        if(checkCaptcha){
            emailjs.sendForm("default_service","desingarte_email",e.target,"user_JNsR1z7JJwp5cRNHvtWbz")
                .then(({text})=>{
                    text==="OK" && setShowNotify(true)
                    },(error)=>{
                        alert(error.text)
                    })
        }else{
            alert("Compruebe que es humano")
        }
    }
    
    return(<>

        <section key="contact" id="contact" className="d-flex" style={{flexWrap:"wrap",backgroundColor:"#fff"}}>
            <div className="d-flex container py-5">
                <div className="col-md-6 h-100 d-flex justify-content-evenly" style={{flexDirection:"column"}}>
                    <div className="text-center text-md-start">
                        <h3 className="" style={{fontSize:"4rem"}}>Contáctanos</h3>
                        <p className="fs-6 w-75 mx-auto mx-md-0" style={{color:"#555"}}>Nuestra empresa digital se encarga de ofrecerte los beneficios de mayor calidad, rapidez y precios que puedes encontrar en el mercado.</p>
                    </div>
                    <form id="contactForm"  action="" onSubmit={ sendMail} className="d-flex w-75 mx-auto mx-md-0 h-100 justify-content-evenly" style={{flexDirection:"column"}}>
                        <div className="d-flex mx-auto mx-md-0 w-100 justify-content-evenly" style={{flexDirection:"column",height:"30rem"}}>
                            <input className="w-100 p-1 border border-2 border-dark" placeholder="Nombre*" name="name" type="text" required/>
                            <input className="w-100 p-1 border border-2 border-dark" placeholder="Correo Electrónico*" name="email" type="email" required />
                            <input className="w-100 p-1 border border-2 border-dark" placeholder="Teléfono*" name="phone" type="tel" required/>
                            <textarea className="w-100 p-1 border border-2 border-dark" placeholder="¿Que servicios requiere?" name="message" id="message" cols="30" rows="5" style={{resize:"none"}}></textarea>
                            <input className="g-recaptcha mx-auto g btn btn-outline-dark rounded-0 border border-2 border-dark" 
                                style={{width:"max-content",transition:"all 0.2s"}} 
                                value="Enviar mensaje"
                                type="submit"
                                data-sitekey="6LeNn-YcAAAAAFY9es4DYrPC2RXrbys9gn-fh9Qg" 
                                data-callback='onSubmit' 
                                data-action='submit'
                                />
                            { showNotify && renderNotification()}

                        </div>
                    </form>
                </div>
                <div className="col-md-6 d-none d-md-flex h-100 justify-content-center" style={{flexDirection:"column"}}> 
                    <img className="w-100 h-50" src={img} alt="persona sentada en su escritorio viendo su celular" />
                </div>
            </div>
            
        </section>
        
    </>)
}

export default Contact